import React from 'react'
import withTranslation from 'Shared/hoc/withTranslations'
import './CareerProPlanSummaryView.scss'

function CareerProPlanSummaryView (props) {
  const { discountedPrice, selectedPlan, t, promoCodeResponse, planQty, planName } = props

  if (!selectedPlan) return 'Loading...' // WIP HOC

  console.log('PLAN_NAME....', planQty, planName)

  const promoDiscountPrice = promoCodeResponse && promoCodeResponse.percentage
  const promoDisPrice = selectedPlan.price * ((100 - promoDiscountPrice) / 100)
  const promoDisPriceFloat = promoDisPrice && parseFloat(promoDisPrice.toFixed(2))
  const discountedPriceFloat = discountedPrice && parseFloat(discountedPrice.toFixed(2))

  console.log('checkout summary controller promo dicount price ....', promoDiscountPrice)
  console.log('checkout summary controller promo price....', promoDisPrice)
  console.log('checkout summary controller discounted price....', discountedPrice)
  let desc = selectedPlan.description.split('|')
  desc = [desc.join('')]

  return (
    <div className='order-review__summary-content'>
      <div className='order-review__summary-product'>
        <div className='order-review__summary-product-amount'>
          {selectedPlan.name}
        </div>
        <div className='order-review__summary-product-description'>
          {`${planQty || ''} ${planName || desc}`}
        </div>
      </div>
      <div className='order-review__summary-price'>
        <div className='order-review__summary-price-label'>{t('careerProPage.summary.total')}</div>
        <div className='order-review__summary-price-amount'>
          <span className={`${(!!discountedPrice || promoDiscountPrice) && 'added_promocode_price'}`}>
            {`$${selectedPlan.price}`}
          </span>
          {(!!discountedPrice || promoDiscountPrice) && `$${discountedPriceFloat || promoDisPriceFloat}`}
        </div>
      </div>
    </div>
  )
}

export default withTranslation(CareerProPlanSummaryView)


