import React, {useState} from 'react'
import removeIcon from '../../../assets/images/icons/removeIcon.svg'
import cardNumbers from '../../../assets/images/icons/cardNumbers.svg'
import visa from '../../../assets/images/icons/visa.png'
import masterCard from '../../../assets/images/icons/Mastercard.svg'
import blankCard from '../../../assets/images/icons/Bankcard.png'
import editPencil from '../../../assets/images/icons/edit_pencil.svg'

const PricingCreditCardView = (props) => {
  const {setRemoveCard, paymentResponse, onRemovePaymentMethod, editCard, setEditCard, className, setFormStep, deleteCardLoader, setDeleteCardLoader} = props

  const [isDisable, setIsDisable] = useState(false)
  const userDetails = JSON.parse(localStorage.getItem('olive-app-user'))

  console.log('CARD EDIT::', paymentResponse)
  console.log('CARD EDIT CONDITION::', editCard)

  const handleSubmit = async () => {
    setIsDisable(true)
    setDeleteCardLoader(true)
    const resp = await onRemovePaymentMethod({ user_id: userDetails.id })
    try {
      if (resp.success) {
        setDeleteCardLoader(false)
        console.log('CARD REMOVE..........')
        setEditCard(true)
        setRemoveCard(true)
      }
      if (!resp.success) {
        console.log('CARD NOT YET..........')
      }
    } catch (error) {
      // console.log('ERROR', error)
    } finally {
      setIsDisable(false)
    }
  }

  const handleEditCard = () => {
    setTimeout(() => {
      document.querySelector('.btnTheme').click()
    }, '100')
  }

  return (
    <div className={`cardEditRemove ${className || ''}`}>
      <div className='cardEdit'>
        <h2>Your saved information</h2>
        <button onClick={() => {
          handleSubmit()
        }} disabled={isDisable}
        ><img src={removeIcon} /></button>
      </div>

      <div className='cardEditDetails'>
        <img src={paymentResponse.data.card.brand == 'visa' ? visa : paymentResponse.data.card.brand == 'mastercard' ? masterCard : blankCard} />
        <h2><span className='hideDetails'>****</span><span>{paymentResponse.data.card.last4}</span></h2>
        <button onClick={() => { handleEditCard(); setEditCard(true) }}><span>(change)</span> <img src={editPencil} /></button>
      </div>
    </div>
  )
}

export default PricingCreditCardView



