import React from 'react'
import { postRequest} from './commonApi'
import { endPoints } from './endPoints'

const useApiRemovePaymentMethod = () => {
  const apiRemovePaymentMethod = async (data) => {
    const url = endPoints.removePaymentMethod

    try {
      let res = await postRequest(url, data)

      if (res.success) {
        return res
      }
    } catch (err) {
      console.log('ERROR', err)
    }
  }

  return { apiRemovePaymentMethod }
}

export default useApiRemovePaymentMethod



