import React, {useState} from 'react'
import Collapse, { Panel } from 'rc-collapse'
import AccordionPanelHeader from 'Shared/components/AccordionPanel/AccordionPanelHeader'
import './PromoCodeView.scss'
import { imagesCareerPro } from '../imagesCareerPro'
import withTranslation from 'Shared/hoc/withTranslations'
import mixpanel from 'mixpanel-browser'
import deleteIcon from '../../../../assets/images/icons/delete.svg'
import { useEffect } from 'react'
import { getLocalData, setLocalData, removeLocalData } from 'Shared/helpers/session'

function PromoCodeView (props) {
  const ICONS = {...imagesCareerPro}
  const { isOpen, promoCodeResponse, setPromoCodeResponse, onApplyPromoCode, onChangePromoCode, t, removeCode, setRemoveCode, promoCodeLoading, getLocalData } = props
  const isValidPromoCode = (promoCodeResponse && promoCodeResponse.promo_code)

  const HEADING = {
    false: t('careerProPage.promocode.input_label'),
    true: t('careerProPage.promocode.input_label_after_applied')
  }

  const [isdisabled, setIsDisabled] = useState('') // For button
  const onChangeDisable = (event) => {
    setIsDisabled(event.target.value)
  }

  const getPromo = getLocalData('promocode')
  console.log('fff', getPromo)

  console.log('resp::::u', isValidPromoCode, 'promo res::', promoCodeResponse)

  const removePromo = () => {
    removeLocalData('promocode_result_defaultCard')
    setRemoveCode(true)
    setPromoCodeResponse(null)
  }

  // useEffect(() => {
  //   removeLocalData('promocode_result_defaultCard')
  // }, [])

  return (<React.Fragment>
    <div className='order-review__promoCode'>
      <Collapse defaultActiveKey={isOpen} className='promo__collapse-container'>
        <Panel showArrow={false} header={<AccordionPanelHeader> {isValidPromoCode && 'Edit'} {isValidPromoCode ? `${HEADING.true}` : HEADING.false} </AccordionPanelHeader>}>
          <div className='promocode_div'>
            <input type='text' name='promoCode'
              defaultValue={promoCodeResponse == null ? '' : promoCodeResponse.promo_code}
              placeholder={t('careerProPage.promocode.input_label_after_applied')}
              onChange={(e) => { onChangeDisable(e); onChangePromoCode(e.target.value) }}
              onBlur={(e) => { onChangeDisable(e); onChangePromoCode(e.target.value, true) }}
              onKeyDown={(e) => onChangeDisable(e)}
              id='getPromoID'
            />
            <button className={`${!isdisabled && 'disabledBtn'}`} onClick={() => { onApplyPromoCode(); mixpanel.track('Button Click - Promo Code - Apply') }}>
              {t('careerProPage.promocode.btn_text')}
            </button>
          </div>
        </Panel>
      </Collapse>

      {promoCodeLoading ? <div class='PromoCodeLoader' /> : ''}

      { isValidPromoCode &&
        <div className='promoCodeApplied'> <img src={ICONS.blue_tick} /> {promoCodeResponse.promo_code} <button className='img_delete' onClick={() => { removePromo() }}><img src={deleteIcon} /></button></div>
      }
      {/* {(promoCodeResponse && !promoCodeResponse.success) ? <span className='promo_code_invalid_msg'>Promo Code Invalid</span>} */}
      {/* {JSON.stringify(removeCode)} */}
      {<div>{!isValidPromoCode && <div>{(promoCodeResponse && !promoCodeResponse.success) && <span className='promo_code_invalid_msg'>Promo code is invalid</span>}</div>}</div>}
    </div>
  </React.Fragment>)
}

export default withTranslation(PromoCodeView)



