import React from 'react'
import Container from 'Shared/components/Container'
import Row from 'Shared/layouts/CareerPro/Components/Row'
import Col from 'Shared/layouts/CareerPro/Components/Col'
import MainHeading from 'Shared/layouts/CareerPro/Components/MainHeading'
import Navbar from 'Shared/layouts/CareerPro/Components/NavLink/NavLink'
import withTranslations from 'Shared/hoc/withTranslations'

import CheckoutSelectedPlan from 'Shared/layouts/CareerPro/CareerProCheckoutPage/CheckoutSelectedPlan'
import FormWrapper from 'Shared/layouts/CareerPro/Components/FormWrapper/FormWrapper'
import StripeElementController from 'Shared/StripeElementController/StripeElementController'
import SummaryWrapper from 'Shared/layouts/CareerPro/CareerProCheckoutPage/SummaryWrapper'
import CareerProCheckoutBenefits from 'Shared/layouts/CareerPro/Components/CareerProCheckoutBenefits/CareerProCheckoutBenefits'
import PricingCheckoutBenefits from './PricingCheckoutBenefits/PricingCheckoutBenefits'
import CheckoutSummaryController from 'Shared/layouts/CareerPro/Components/CheckoutSummary/CheckoutSummaryController'
import { getLocalData } from 'Shared/helpers/session'
import './PricingCheckoutPage.scss'
import { endPoints } from '../../../services/api/endPoints'

const PricingCheckoutView = (props) => {
  const { t, organisation, promoCodeUserInput, promoCodeResponse, onApplyPromoCode, onChangePromoCode, paymentResponse, onRemovePaymentMethod, editCard, setEditCard,
    PromoCodeView, formStep, setFormStep = () => {}, url, userDetails } = props

  const selectedPlan = getLocalData('selected_package')
  console.log('pricing checkout view.... page.....', promoCodeResponse)

  const planQty = selectedPlan.features
  const planName = selectedPlan.name

  console.log('PLAN_NAME', planQty, planName)

  const pageUrl = window.location.pathname
  const pricingPage = pageUrl === '/pricing/checkout'
  const pricingReturnUrl = `${process.env.REACT_APP_ROOT_URL}/pricing`
  // const pricingReturnUrl = `http://linkst.local:7001/pricing`
  const updateCreditCard = endPoints.addCreditCardPayment
  const returnUrl = !paymentResponse && pricingPage ? pricingReturnUrl : paymentResponse && paymentResponse.success ? updateCreditCard : pricingReturnUrl
  console.log('prcing page return url....', returnUrl)

  return (<Container className='order-review__container'>
    <Row>
      <Col col={5} xs={12} sm={12} md={12}>
        <MainHeading />
        <Navbar
          name={t('pricingPage.checkout.Navlink.pricing')}
          link={'/pricing'}
        />
        <CheckoutSelectedPlan planQty={planQty} selectedPlan={selectedPlan} />

        <FormWrapper>
          <StripeElementController organisation={organisation} returnUrl={returnUrl} promoCodeUserInput={promoCodeUserInput} promoCodeResponse={promoCodeResponse} paymentResponse={paymentResponse} onRemovePaymentMethod={onRemovePaymentMethod}
            formStep={formStep} setFormStep={setFormStep} url={url} userDetails={userDetails} editCard={editCard} setEditCard={setEditCard}
          />
        </FormWrapper>
      </Col>
      <Col col={1} xs={12} sm={12} md={12}></Col>
      <Col col={6} xs={12} sm={12} md={12} >
        <div>
          <SummaryWrapper>
            <CheckoutSummaryController planQty={planQty} planName={planName} promoCodeResponse={promoCodeResponse} editCard={editCard} selectedPlan={selectedPlan} />
            <PromoCodeView
              promoCodeUserInput={promoCodeUserInput}
              promoCodeResponse={promoCodeResponse}
              onApplyPromoCode={onApplyPromoCode}
              onChangePromoCode={onChangePromoCode}
            />
          </SummaryWrapper>

          {/* <CareerProCheckoutBenefits
            selectedPlan={selectedPlan} /> */}
          <PricingCheckoutBenefits selectedPlan={selectedPlan} />
        </div>
      </Col>
    </Row>
  </Container>)
}

export default withTranslations(PricingCheckoutView)



