import React from 'react'
import { NavLink } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import './Navbar.scss'

const Navbar = (props) => {
  const {t, name, link} = props
  return (
    <div className='order-review__steps career_pro_checkout'>
      <NavLink
        className='order-review__steps-link grey_color_deactive'
        to={link}>
        {name}
      </NavLink>

      <NavLink
        className='order-review__steps-link Nolink'
        activeClassName='order-review__steps-link--active'
        to={'/career-pro/checkout'}>
        {t('careerProPage.checkout.Navlink.checkout')}
      </NavLink>

    </div>
  )
}
export default withTranslations(Navbar)



