import { useState } from 'react'
import { getRequest } from './commonApi'
import { endPoints } from './endPoints'
import { setLocalData, getLocalData } from 'Shared/helpers/session'

const useApiPaymentStatus = () => {
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState(null)
  const getUserId = JSON.parse(localStorage.getItem('olive-app-user'))

  const apiPaymentStatus = async () => {
    const url = endPoints.paymentStatus
    const data = {
      user_id: getUserId.id || ''
    }

    setLoading(true)
    try {
      let res = await getRequest(url, data)
      if (res.success) {
        setData(res.data)
        console.log('PAYMENT___STATUS::::', res)
        setLoading(false)
        return res.data
      }
    } catch (err) {
      console.log('Err', err)
      setLoading(false)
    }
  }
  return { loading, data, apiPaymentStatus }
}

export default useApiPaymentStatus




