import React from 'react'

export default function CheckoutSelectedPlan (props) {
  const { selectedPlan, planQty } = props

  let desc = selectedPlan && selectedPlan.description && selectedPlan.description.split('|')
  desc = desc && [desc.join('')]
  const pageUrl = window.location.pathname
  const pricingPage = pageUrl === '/pricing/checkout'

  if (!selectedPlan) return <div>Loading...</div>

  return (
    <div>
      {pricingPage ? <div className='order-review__selected'>
        <h2>{selectedPlan.name}</h2>
        <p>{planQty || selectedPlan.quantity} {selectedPlan.name} - <span>{`$${selectedPlan.price}`}</span></p>
      </div> : <div className='order-review__selected'>
        <h2>{selectedPlan.name}</h2>
        <p>{desc} - <span>{`$${selectedPlan.price}`}</span></p>
      </div>}
      <hr className='order-review__hr' />
    </div>
  )
}



