import { useState, useEffect } from 'react'
import { getRequest } from './commonApi'
import { endPoints } from './endPoints'
import { setLocalData } from 'Shared/helpers/session'

const useApiPaymentMethodDetails = () => {
  const [loading, setLoading] = useState(true)
  const [data, setData] = useState(null)

  const apiPaymentMethodDetails = async (userId, url = endPoints.paymentMethodDetails) => {
    try {
      let res = await getRequest(url, { user_id: userId })

      if (res.success) {
        setData(res.res)
        setLocalData('payment_method_res', res.data)
        return res
      }

      if (res.success == false) {
        setData(res.res)
        setLocalData('payment_method_res', res)
        return res
      }

      setLocalData('payment_method_res', null)
    } catch (err) {
      console.log('Err', err)
    }
  }

  useEffect(() => {

  }, [])

  return { loading, data, apiPaymentMethodDetails }
}

export default useApiPaymentMethodDetails



