import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { isFreePlansPage } from 'Shared/selectors/geolocation'
import PricingCheckoutPage from 'Shared/layouts/Plans/PricingCheckoutPage/PricingCheckoutPage'
import { getUserDetails } from 'Shared/selectors/user'
import { getUsersOrganisation } from 'Shared/selectors/organisations'

const mapStateToProps = state => ({
  userRole: state.user.role,
  isFreePlansPage: isFreePlansPage(state),
  getUserDetails: getUserDetails(state),
  organisation: getUsersOrganisation(state)
})

export default withRouter(connect(mapStateToProps)(PricingCheckoutPage))



