import { useState, useEffect } from 'react'
import useApiAddPromoCode from 'Shared/services/api/useApiAddPromoCode'
import { getLocalData, setLocalData } from 'Shared/helpers/session'

const PromoCodeModel = () => {
  const [promoCodeUserInput, setPromoCodeUserInput] = useState('')
  const [promoCodeResponse, setPromoCodeResponse] = useState('')
  const [promoCodeLoading, setPromoCodeLoading] = useState(false)
  const [removeCode, setRemoveCode] = useState(false)
  const { apiAddPromoCode } = useApiAddPromoCode()

  const hasLocalPromoCode = getLocalData('promocode')
  const hasLocalPromoCodeResult = getLocalData('promocode_result')

  const onChangePromoCode = (newValue, saveInState = false) => {
    setLocalData('promocode', newValue)
  }

  const onApplyPromoCode = async (url) => {
    setPromoCodeLoading(true)
    const newPromoCode = getLocalData('promocode')
    apiAddPromoCode(newPromoCode, url).then(resp => {
      setPromoCodeResponse(resp)
      setPromoCodeLoading(false)
    })

    await setPromoCodeUserInput(newPromoCode)
  }

  useEffect(() => {
    setPromoCodeUserInput(hasLocalPromoCode)
  }, [promoCodeUserInput])

  useEffect(() => {
    setPromoCodeResponse(hasLocalPromoCodeResult)
  }, [])

  return {
    promoCodeUserInput,
    setPromoCodeUserInput,
    promoCodeResponse,
    setPromoCodeResponse,
    onChangePromoCode,
    onApplyPromoCode,
    removeCode,
    setRemoveCode,
    promoCodeLoading,
    getLocalData
  }
}

export default PromoCodeModel



