import React, { useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import StripeElementView from './StripeElementView'
import StripeElementModel from './StripeElementModel'
import withReduxUser from './withReduxUser'
import { getLocalData, setLocalData, removeLocalData } from 'Shared/helpers/session'
import useApiCreateCareerProUserInfo from '../services/api/useApiCreateCareerProUserInfo'
import mixpanel from 'mixpanel-browser'
import { endPoints } from '../services/api/endPoints'

function StripeElementController (props) {
  const { organisation, promoCodeResponse, promoCodeUserInput, url, userDetails, paymentResponse, onRemovePaymentMethod, editCard, setEditCard, showFormCareerPro, returnUrl } = props
  const selectedPackage = getLocalData('selected_package') || null
  const creditType = selectedPackage && selectedPackage.name
  const selectedPackageId = selectedPackage && selectedPackage.id
  // const pageUrl = window.location.pathname
  // const pricingPage = pageUrl === '/pricing/checkout'

  // const pricingReturnUrl = `${process.env.REACT_APP_ROOT_URL}/pricing?redirect_status=succeeded`
  // const updateCreditCard = endPoints.addCreditCardPayment
  // const careerProLink = `${process.env.REACT_APP_ROOT_URL}/career-pro`
  // const returnUrl = updateCreditCard || careerProLink
  // const returnUrl = !paymentResponse && pricingPage ? pricingReturnUrl : paymentResponse && paymentResponse.success ? updateCreditCard : careerProLink
  console.log('RETURN____URL', returnUrl)
  const {
    data, apiStripeIntentModel,
    formStep, setFormStep, stripeUserForm, setStripeUserForm, isValidateUserForm,
    stripeUserFormError, deleteCardLoader, setDeleteCardLoader, onDeafultCreditCard, loading, setLoading, removeCard, setRemoveCard,
    removeCardMsg, setRemoveCardMsg
  } = StripeElementModel()
  const { apiCreateCareerProUserInfo } = useApiCreateCareerProUserInfo()

  const handleApiStripeIntent = (pkgId) => {
    apiCreateCareerProUserInfo()
    apiStripeIntentModel(pkgId, promoCodeUserInput, true, stripeUserForm.name, stripeUserForm.email, stripeUserForm.phone, null, url, creditType, userDetails );
    mixpanel.track('Career Pro - Form Details - Button Click - Next')
  }

  const fromChangeHandler = (e) => {
    const { value, name, type } = e.target
    setLocalData(`stripe_form_${name}`, value)

    setStripeUserForm((prevState) => ({
      ...prevState,
      [name]: value,
      errors: stripeUserFormError
    }))
  }

  console.log('promo_code_res...', promoCodeResponse)

  // Default Credit Card Start
  const getUserId = JSON.parse(localStorage.getItem('olive-app-user'))
  const getSelectedPkg = getLocalData('selected_package')
  // const getPromoCode = getLocalData('promocode_result_defaultCard')

  console.log('organisation', organisation)

  const handleDefaultCreditCard = async () => {
    setLoading(true)
    const hasPromoCodeObj = (promoCodeResponse && promoCodeResponse.promo_code && promoCodeResponse.coupon_id) && {
      promo_code: (promoCodeResponse && promoCodeResponse.promo_code),
      coupon_id: (promoCodeResponse && promoCodeResponse.coupon_id)
    }
    const resp = await onDeafultCreditCard({
      user_id: getUserId.id || '',
      organisation_id: organisation.id || '',
      package_id: getSelectedPkg.id || '',
      is_completed: true,
      ...hasPromoCodeObj
    })
    try {
      if (resp.success) {
        setLoading(false)
        props.history.push('/pricing?redirect_status=succeeded')
        removeLocalData('promocode_result_defaultCard')
        console.log('PKG SUBSCRIBE..........')
      }
      if (!resp.success) {
        setLoading(false)
        removeLocalData('promocode_result_defaultCard')
        setRemoveCardMsg('Something Went Wrong')
        console.log('PKG NOT SUBSCRIBE..........')
      }
    } catch (error) {
      setRemoveCardMsg('Something Went Wrong')
      console.log('ERROR', error)
    } finally {
      setLoading(false)
    }
  }
  // Default Credit Card End

  useEffect(() => {
    ((stripeUserForm && stripeUserForm.email) && promoCodeUserInput) && handleApiStripeIntent(selectedPackageId)
  }, [promoCodeUserInput])

  const StripeElementViewWithRedux = withReduxUser(StripeElementView)

  return (<React.Fragment>
    <StripeElementViewWithRedux
      deleteCardLoader={deleteCardLoader}
      setDeleteCardLoader={setDeleteCardLoader}
      formStep={formStep} setFormStep={setFormStep}
      paymentResponse={paymentResponse}
      isValidateUserForm={isValidateUserForm}
      stripeUserForm={stripeUserForm} setStripeUserForm={setStripeUserForm}
      selectedPackageId={selectedPackageId}
      clientSecret={data ? data.client_secret : null}
      handleApiStripeIntent={handleApiStripeIntent}
      fromChangeHandler={fromChangeHandler}
      onRemovePaymentMethod={onRemovePaymentMethod}
      editCard={editCard} setEditCard={setEditCard}
      showFormCareerPro={showFormCareerPro}
      returnUrl={returnUrl}
      handleDefaultCreditCard={handleDefaultCreditCard}
      loading={loading}
      setLoading={setLoading}
      removeCard={removeCard}
      setRemoveCard={setRemoveCard}
      removeCardMsg={removeCardMsg}
      setRemoveCardMsg={setRemoveCardMsg}
    />
  </React.Fragment>)
}

export default withRouter(StripeElementController)



