import React from 'react'
import { withRouter } from 'react-router-dom'
import customPlan from 'Shared/assets/images/illustrations/custom-plans.svg'
import withTranslations from 'Shared/hoc/withTranslations'
import MediaPlan from './MediaPlan'

const CustomPlans = ({ t, history }) => {
  const onHandleContact = () => {
    history.push('/contact')
  }

  return (
    <div className='custom-plan-main-container'>
      <div className='columns custom-plan-sub-container-one'>
        <div className='column custom-plan-sub-container-two'>
          <div className='custom-plans-sub-heading'>{t('customPlans.option')}</div>
          <div className='custom-plans-heading'>{t('customPlans.title')}</div>
          <div className='custom-plans-description'>{t('customPlans.description')}</div>

          <button className='custom-plans-button' onClick={onHandleContact}>
                    Talk to sales
          </button>
          <div className='media-plan-wrapper'>
            <MediaPlan />
          </div>

        </div>
        <div className='column custom-plan-image'>
          <img src={customPlan} />
        </div>

      </div>
    </div>
  )
}
export default withTranslations(withRouter(CustomPlans))



