import { useEffect, useState } from 'react'
import useApiPaymentMethodDetails from 'Shared/services/api/useApiPaymentMethodDetails'
import useApiRemovePaymentMethod from 'Shared/services/api/useApiRemovePaymentMethod'
import { getLocalData, setLocalData } from 'Shared/helpers/session'
import PromoCodeModel from '../../CareerPro/Components/PromoCode/PromoCodeModel'
import useApiPricingPaymentIntent from '../../../services/api/useApiPricingPaymentIntent'
import useApiAddCreditCard from '../../../services/api/useApiAddCreditCard'

const PricingCheckoutModel = (props) => {
  const [formStep, setFormStep] = useState(formStep || 1)
  const [paymentResponse, setPaymentResponse] = useState('')
  const { apiPaymentMethodDetails } = useApiPaymentMethodDetails()
  const { apiRemovePaymentMethod } = useApiRemovePaymentMethod()
  const [editCard, setEditCard] = useState(false)
  const userDetails = JSON.parse(localStorage.getItem('olive-app-user'))
  const hasUserLogin = JSON.parse(localStorage.getItem('ajs_user_id'))

  // const { apiPricingPaymentIntent } = useApiPricingPaymentIntent()
  const { apiAddCreditCard } = useApiAddCreditCard()
  console.log('user details:', userDetails)

  console.log('user__id', hasUserLogin)
  const { promoCodeResponse } = PromoCodeModel()

  // Get Payment Method

  const onPaymentMethod = async (url) => {
    apiPaymentMethodDetails(userDetails.id, url).then(resp => {
      setPaymentResponse(resp)
    })
  }

  // Remove Payment Method

  const onRemovePaymentMethod = async (arg) => {
    return apiRemovePaymentMethod(arg)
  }

  useEffect(() => {
  }, [promoCodeResponse])

  // useEffect(() => {
  //   // apiAddCreditCard()
  //   if (hasUserLogin == null) {
  //     window.location.href = `${process.env.REACT_APP_ROOT_URL}/employer/login`
  //   }
  // }, [])

  return { formStep,
    setFormStep,
    onPaymentMethod,
    paymentResponse,
    setPaymentResponse,
    onRemovePaymentMethod,
    editCard,
    setEditCard }
}

export default PricingCheckoutModel



