import React, {useEffect} from 'react'
import PromoCodeController from '../../../layouts/CareerPro/Components/PromoCode/PromoCodeController'
import PricingCheckoutView from './PricingCheckoutView'
import { endPoints } from 'Shared/services/api/endPoints'
import PromoCodeModel from '../../CareerPro/Components/PromoCode/PromoCodeModel'
import PricingCheckoutModel from './PricingCheckoutModel'

const PricingCheckoutController = (props) => {
  const { onApplyPromoCode } = PromoCodeModel()
  const {userDetails, organisation} = props
  const {onPaymentMethod, paymentResponse, setPaymentResponse, onRemovePaymentMethod, editCard, setEditCard} = PricingCheckoutModel()
  const { view: PromoCodeView, promoCodeUserInput, promoCodeResponse } = PromoCodeController()

  console.log('p__checkout_controller', promoCodeResponse)
  const url = endPoints.pricingPaymentIntent

  // On Apply Promo Code
  const onApplyPromoCodeController = async (arg) => {
    try {
      const res = onApplyPromoCode(arg)
      return res
    } catch (error) {
      console.log(error)
    }
  }

  // On Remove Payment Method
  const onRemovePaymentMethodController = async (arg) => {
    try {
      const res = onRemovePaymentMethod(arg)
      return res
    } catch (error) {
      console.log(error)
    }
  }

  // On Payment Method
  const onPaymentMethodController = async (arg) => {
    try {
      const res = onPaymentMethod(arg)
      console.log('updated.......')
      return res
    } catch (error) {
      console.log(error)
    }
  }
  useEffect(() => {
    onPaymentMethodController()
  }, [])

  console.log('paymentResponse::', paymentResponse)

  return (<React.Fragment>
    <PricingCheckoutView
      promoCodeResponse={promoCodeResponse}
      organisation={organisation}
      PromoCodeView={PromoCodeView} paymentResponse={paymentResponse} onApplyPromoCode={onApplyPromoCodeController} promoCodeUserInput={promoCodeUserInput} url={url} userDetails={userDetails} onRemovePaymentMethod={onRemovePaymentMethodController} editCard={editCard} setEditCard={setEditCard}
    />
  </React.Fragment>)
}

export default PricingCheckoutController



