export const MODAL_DATA = {
  'success_login_user': {
    'submitText': 'Update Profile',
    'cancelText': 'Continue',
    'handleSubmit': () => window.location = '/candidate/profile/preview',
    'showSubmit': true,
    'showCancel': true,
    'status': 'success',
    'heading': 'Your payment was successful!',
    'des': `Thank you for your payment. Your credits will be automatically activated. For any clarifications, please feel free to reach us <a href='mailto:team@oliv.com'>team@oliv.com</a>.`,
  },
  'success_nonlogin_user': {
    'submitText': 'Sign Up',
    'cancelText': 'Continue',
    'showSubmit': true,
    'handleSubmit': () => window.location = '/candidate/sign-up',
    'showCancel': true,
    'status': 'success',
    'heading': 'Your payment was successful!',
    'des': `Thank you for your payment. Your credits will be automatically activated. For any clarifications, please feel free to reach us <a href='mailto:team@oliv.com'>team@oliv.com</a>.`,
  },
  'failed': {
    'submitText': 'Retry',
    'cancelText': 'Cancel',
    'handleSubmit': () => window.location = '/career-pro/',
    'showSubmit': true,
    'showCancel': true,
    'status': 'fail',
    'heading': 'Payment has been declined',
    'des': `Something went wrong and your payment has not been proceeded. For any clarifications, please feel free to reach us <a href='mailto:team@oliv.com'>team@oliv.com</a>.`,
  }
}
  
export const PAYMENT_STATUS_TYPE = {
  'succeeded': 'success',
  'failed': 'fail'
}


