import React from 'react'
import { postRequest} from './commonApi'
import { endPoints } from './endPoints'

const useApiDafaultCreditCard = () => {
  const apiDafaultCreditCard = async (data) => {
    const url = endPoints.defaultCreditCard

    try {
      let res = await postRequest(url, data)

      if (res.success) {
        return res
      }
    } catch (err) {
      console.log('ERROR', err)
    }
  }

  return { apiDafaultCreditCard }
}

export default useApiDafaultCreditCard



