import React from 'react'
import { withRouter } from 'react-router-dom'
import {Elements} from '@stripe/react-stripe-js'
import {loadStripe} from '@stripe/stripe-js'
import StripeCheckoutForm from './StripeCheckoutForm'
import UserInfoView from './UserInfo/UserInfoView'
import { imagesCareerPro } from 'Shared/layouts/CareerPro/Components/imagesCareerPro'
import { OnlyBoxLoader } from 'Shared/helpers/skeletonLoader'
import PricingCreditCardView from '../layouts/Plans/PricingCheckoutPage/PricingCreditCardView'

const stripePromise = loadStripe(process.env.REACT_APP_STRIPE_KEY)
const pageUrl = window.location.pathname

function StripeElementView (props) {
  const { removeCardMsg, setRemoveCardMsg, removeCard, setRemoveCard, loading, setLoading, handleDefaultCreditCard, deleteCardLoader, setDeleteCardLoader, cardEditBtn, selectedPackageId, formStep, setFormStep, stripeUserForm, setStripeUserForm, handleApiStripeIntent, fromChangeHandler, paymentResponse, onRemovePaymentMethod, editCard, setEditCard, showFormCareerPro, returnUrl } = props
  const ICONS = {...imagesCareerPro}
  const options = {
    clientSecret: props.clientSecret
  }

  const paymentRes = paymentResponse && paymentResponse.success

  const redirectToPricing = () => props.history.push('/pricing')

  return (<React.Fragment>
    <div className={`content ${(formStep === 1) && 'active-content'}`}>
      {paymentRes && <PricingCreditCardView setRemoveCard={setRemoveCard} deleteCardLoader={deleteCardLoader} setDeleteCardLoader={setDeleteCardLoader} cardEditBtn={cardEditBtn} setFormStep={setFormStep} className={`${editCard && 'payment_card_edit_data'}`} paymentResponse={paymentResponse} onRemovePaymentMethod={onRemovePaymentMethod} editCard={editCard} setEditCard={setEditCard} />}
      <UserInfoView removeCard={removeCard} deleteCardLoader={deleteCardLoader} setDeleteCardLoader={setDeleteCardLoader} cardEditBtn={cardEditBtn} className={`${paymentRes && 'user_info_hide_data'} ${editCard && 'user_info_show_data'}`} stripeUserForm={stripeUserForm} setStripeUserForm={setStripeUserForm} fromChangeHandler={fromChangeHandler} editCard={editCard} formStep={formStep} setFormStep={setFormStep} handleApiStripeIntent={handleApiStripeIntent} selectedPackageId={selectedPackageId} paymentResponse={paymentResponse} showFormCareerPro={showFormCareerPro} />
      
      <div>{paymentRes && <div className={`btn_subscribe_loader ${editCard && 'payment_card_edit_data'}`}>
        <div class={`PromoCodeLoader loaderHideSubscribe ${loading && 'loaderShowSubscribe'}`} />
        <button disabled={loading} className={`btnTheme btn_stripe btn_theme__subscribe ${loading && 'btn_opacity_none'}`} onClick={() => handleDefaultCreditCard()}>Continue</button>
      </div>}
      </div>
      {removeCardMsg && <h4 className='msg__wrong'>{removeCardMsg}</h4>}
    </div>

    <div className={`content ${(formStep === 2) && 'active-content' }`}>
      { options.clientSecret
        ? <React.Fragment>
          <button className={`${(formStep === 1) && 'tabs-active'} tabs back_step_arrow`} onClick={() => pageUrl === '/pricing/checkout' ? redirectToPricing() : setFormStep(1)}>
            <img src={ICONS.slider_left_arrow} alt='back icon' /> Back
          </button>
          <Elements stripe={stripePromise} options={options}>
            <StripeCheckoutForm package_id={selectedPackageId} stripeUserForm={stripeUserForm} returnUrl={returnUrl} />
          </Elements>
        </React.Fragment> : <div>
          <OnlyBoxLoader />
        </div>
      }
    </div>
  </React.Fragment>)
};

export default withRouter(StripeElementView)



