// import { useState } from "react";
import { getRequest } from "./commonApi";
import { endPoints } from "./endPoints";
import { setLocalData } from "Shared/helpers/session";

const useApiGetProduct = () => {
  
//   const [loading, setLoading] = useState(true);
//   const [data, setData] = useState(null);
  
  const apiGetProduct = async () => {

    const url = endPoints.getProduct
  
    try {

      let res = await getRequest(url);
 
      if(res.success) {
        // setData(res.data || res)
        setLocalData('product_details', res.data)
        return res.data;
      }

    } catch(err) {
      console.log("Err", err);
    }

  };

  return {  apiGetProduct };

};

export default useApiGetProduct;


