import React, { useState, useEffect } from 'react'
import './UserInfo.scss'
import withTranslation from 'Shared/hoc/withTranslations'
import { isValidValue, isEmptyObject } from 'Shared/helpers/validator'
import { setLocalData, getSessionData } from 'Shared/helpers/session'
import ContentLoader from 'react-content-loader'

function UserInfoView (props) {
  const { stripeUserForm, removeCard, setStripeUserForm, selectedPackageId, formStep, setFormStep, handleApiStripeIntent, className, paymentResponse, showFormCareerPro, t, deleteCardLoader } = props
  const [inputs, setInputs] = useState({})
  const [formError, setFormError] = useState({name: '', email: '', phone: ''})
  const [isValidForm, setIsValidForm] = useState(false)
  const bliingDetails = getSessionData('user_detail')

  const ERROR = {
    name: formError.name,
    email: formError.email,
    phone: formError.phone
  }

  const isValidateUserFormHandler = () => {
    const noErrors = Object.values(formError).every((v) => v === true)
    if (noErrors) { setIsValidForm(true); return true }

    setIsValidForm(false)
    return false
  }

  const handleInputChange = (e) => {
    const { value, name, type } = e.target
    setLocalData(`stripe_form_${name}`, value)
    setInputs(prev => ({ ...prev, [name]: value }))
    setFormError((prev) => ({ ...prev, [name]: !!isValidValue(value, type, true) }))
    isValidateUserFormHandler()
  }

  const handleSubmit = () => {
    if (!isValidForm) return
    setFormStep(2)
    setStripeUserForm({ name: inputs.name, email: inputs.email, phone: inputs.phone })
    handleApiStripeIntent(selectedPackageId)
  }

  useEffect(() => {
    (stripeUserForm.name && stripeUserForm.email && stripeUserForm.phone) && setIsValidForm(true)
  }, [])

  useEffect(() => {
    setIsValidForm(true)
  }, [props.editCard])

  return (<React.Fragment>
    {deleteCardLoader ? <div className='cardRemoveDiv'>
      <div class='PromoCodeLoader' />
      {/* <h3>Card has been Removed</h3> */}
    </div> : ''}
    {paymentResponse ? <form autoComplete='off' className={className || ''}>
      <div className='order-review__user_form pricing_user_form'>
        <h3>{t('careerProPage.checkout.form.title', 'Enter your details')}</h3>
        <div className={`form-group ${(stripeUserForm.name || bliingDetails.name) && 'pricing_user_form_disable'}`} data-valid={ERROR['name']} >
          <label>{t('careerProPage.checkout.form.full_name_label', 'Full Name')}</label>
          <input name='name' type='text' placeholder={t('careerProPage.checkout.form.full_name_placeholder', 'Your Name')} defaultValue={stripeUserForm.name || bliingDetails.name}
            onKeyUp={(e) => handleInputChange(e)}
            onChange={(e) => handleInputChange(e)}
            required
            disabled={removeCard || stripeUserForm.name}
            autocomplete='none'
          />
        </div>

        <div className={`form-group ${(stripeUserForm.email || bliingDetails.email) && 'pricing_user_form_disable'}`} data-valid={ERROR['email']}>
          <label>{t('careerProPage.checkout.form.email_label', 'Email')}</label>
          <input name='email' type='email' placeholder={t('careerProPage.checkout.form.email_placeholder', 'Email Address')}
            defaultValue={stripeUserForm.email || bliingDetails.email}
            onKeyUp={(e) => handleInputChange(e)}
            onChange={(e) => handleInputChange(e)}
            required
            disabled={removeCard || stripeUserForm.email}
            autocomplete='none'
          />
        </div>

        <div className={`form-group ${(stripeUserForm.phone || bliingDetails.phone) && 'pricing_user_form_disable'}`} data-valid={ERROR['phone']}>
          <label>{t('careerProPage.checkout.form.phone_label', 'Phone Number')}</label>
          <input name='phone' type='tel' placeholder={t('careerProPage.checkout.form.phone_placeholder', 'Phone Number')}
            defaultValue={stripeUserForm.phone || bliingDetails.phone}
            onKeyUp={(e) => handleInputChange(e)}
            onChange={(e) => handleInputChange(e)}
            required
            disabled={removeCard || stripeUserForm.phone}
            autocomplete='none'
            onKeyPress={(event) => {
              if (!/[0-9]/.test(event.key)) {
                event.preventDefault()
              }
            }}
          />
        </div>
      </div>

      <button className={`${(formStep === 2) && 'tabs-active'} ${!isValidForm && 'disabled_btn'} tabs btnTheme`}
        onClick={() => {
          handleSubmit()
        }}
        disabled={!isValidForm}
      >
        { isValidForm ? 'Next' : 'Next' }
      </button>
    </form> : showFormCareerPro ? <form autoComplete='off' className={className || ''}>
      <div className='order-review__user_form'>
        <h3>{t('careerProPage.checkout.form.title', 'Enter your details')}</h3>
        <div className='form-group' data-valid={ERROR['name']} >
          <label>{t('careerProPage.checkout.form.full_name_label', 'Full Name')}</label>
          <input name='name' type='text' placeholder={t('careerProPage.checkout.form.full_name_placeholder', 'Your Name')} defaultValue={stripeUserForm.name || (bliingDetails && bliingDetails.name)}
            onKeyUp={(e) => handleInputChange(e)}
            onChange={(e) => handleInputChange(e)}
            required
            autocomplete='none'
          />
        </div>

        <div className='form-group' data-valid={ERROR['email']}>
          <label>{t('careerProPage.checkout.form.email_label', 'Email')}</label>
          <input name='email' type='email' placeholder={t('careerProPage.checkout.form.email_placeholder', 'Email Address')}
            defaultValue={stripeUserForm.email || (bliingDetails && bliingDetails.email)}
            onKeyUp={(e) => handleInputChange(e)}
            onChange={(e) => handleInputChange(e)}
            required
            autocomplete='none'
          />
        </div>

        <div className='form-group' data-valid={ERROR['phone']}>
          <label>{t('careerProPage.checkout.form.phone_label', 'Phone Number')}</label>
          <input name='phone' type='tel' placeholder={t('careerProPage.checkout.form.phone_placeholder', 'Phone Number')}
            defaultValue={stripeUserForm.phone || (bliingDetails && bliingDetails.phone)}
            onKeyUp={(e) => handleInputChange(e)}
            onChange={(e) => handleInputChange(e)}
            required
            autocomplete='none'
            onKeyPress={(event) => {
              if (!/[0-9]/.test(event.key)) {
                event.preventDefault()
              }
            }}
          />
        </div>
      </div>

      <button className={`${(formStep === 2) && 'tabs-active'} ${!isValidForm && 'disabled_btn'} tabs btnTheme`}
        onClick={() => {
          handleSubmit()
        }}
        disabled={!isValidForm}
      >
        { isValidForm ? 'Next' : 'Next' }
      </button>
    </form> : <ContentLoader
      speed={1}
      width={340}
      height={84}
      viewBox='0 0 340 84'
      backgroundColor='#f6f6ef'
      foregroundColor='#e8e8e3'
    >
      <rect x='9' y='4' rx='0' ry='0' width='320' height='22' />
      <rect x='18' y='14' rx='0' ry='0' width='303' height='6' />
      <rect x='11' y='33' rx='0' ry='0' width='108' height='13' />
      <rect x='129' y='33' rx='0' ry='0' width='60' height='13' />
      <rect x='196' y='33' rx='0' ry='0' width='60' height='13' />
    </ContentLoader>}
  </React.Fragment>)
}

export default withTranslation(UserInfoView)



