import { useState, useEffect } from 'react'
import { getRequest } from './commonApi'
import { endPoints } from './endPoints'
import { setLocalData } from 'Shared/helpers/session'

const useApiAddCreditCard = () => {
  const [loading, setLoading] = useState(true)
  const [data, setData] = useState(null)

  const apiAddCreditCard = async (url = endPoints.addCreditCardPayment) => {
    try {
      let res = await getRequest(url)

      if (res.success) {
        setData(res.res)
        console.log('add credit card::success', res)
        // setLocalData('payment_method_res', res.data)
        return res
      }

      if (res.success == false) {
        setData(res.res)
        console.log('add credit card::false', res)
        // setLocalData('payment_method_res', res)
        return res
      }

      // setLocalData('payment_method_res', null)
    } catch (err) {
      console.log('Err', err)
    }
  }

  useEffect(() => {

  }, [])

  return { loading, data, apiAddCreditCard }
}

export default useApiAddCreditCard



