import React from 'react'
import PricingCheckoutController from './PricingCheckoutController'

export default function PricingCheckoutPage (props) {
  const {getUserDetails, organisation} = props
  
  return (<React.Fragment> <PricingCheckoutController organisation={organisation} userDetails={getUserDetails} /> </React.Fragment>)
}



