import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { isFreePlansPage } from 'Shared/selectors/geolocation'
import Plans from 'Shared/layouts/Plans/Plans'
// import { getOrganisationPerksById, getOrganisation } from 'Shared/selectors/organisations'
import { getUsersOrganisation } from 'Shared/selectors/organisations'

const mapStateToProps = (state, ownProps) => ({
  userRole: state.user.role,
  isFreePlansPage: isFreePlansPage(state),
  // perks: getOrganisationPerksById(ownProps.match.params.id)(state),
  organisation: getUsersOrganisation(state)
  // organisation: getOrganisation(state, ownProps.match.params.id)
})

export default withRouter(connect(mapStateToProps)(Plans))



