import { useEffect, useState } from 'react'
import useApiStripeIntent from '../services/api/useApiStripeIntent'
import { debounce } from 'Shared/helpers/debounce'
import useApiDafaultCreditCard from '../services/api/useApiDafaultCreditCard'

export default function StripeElementModel () {
  const userInfoDefault = { name: '', email: '', phone: '' }
  const { data, apiRequest: apiStripeIntent } = useApiStripeIntent()
  const { apiDafaultCreditCard } = useApiDafaultCreditCard()
  const [stripeUserForm, setStripeUserForm] = useState({userInfoDefault})
  const [stripeUserFormError, setStripeUserFormError] = useState(false)
  const [deleteCardLoader, setDeleteCardLoader] = useState(false)

  const [formStep, setFormStep] = useState(formStep || 1)
  const [isValidateUserForm, setIsValidateUserForm] = useState(false)
  const [loading, setLoading] = useState(false)
  const [removeCard, setRemoveCard] = useState(false)
  const [removeCardMsg, setRemoveCardMsg] = useState('')

  const apiStripeIntentModel = (packageId, promoCode, is_new = true, name, email, phone, redirectTo = null, url, creditType, userDetails) => {
    apiStripeIntent(packageId, promoCode, is_new=true, name, email, phone, null, url, creditType, userDetails) // This order should be same with useApiStripeIntent
  }

  const isValidate = (form) => {
    let validated = (form) && (!!form.name === true && !!form.email === true && !!form.phone === true)
    return validated
  }

  const onDeafultCreditCard = async (arg) => {
    return apiDafaultCreditCard(arg)
  }

  useEffect(() => {
    debounce(setIsValidateUserForm(isValidate(stripeUserForm)), 4000)
  }, [stripeUserForm])

  return {
    formStep,
    setFormStep,
    data,
    apiStripeIntentModel,
    stripeUserForm,
    setStripeUserForm,
    isValidateUserForm,
    stripeUserFormError,
    setStripeUserFormError,
    deleteCardLoader,
    setDeleteCardLoader,
    onDeafultCreditCard,
    loading,
    setLoading,
    removeCard,
    setRemoveCard,
    removeCardMsg,
    setRemoveCardMsg
  }
}



