import { connect } from 'react-redux'
import React, { useState, useEffect } from 'react'
import { getUserDetails } from 'Shared/selectors/user'
import { setLocalData, getLocalData } from 'Shared/helpers/session'

export default function withReduxUser (OriginalComponent) {
  const NewComponent = (props) => {
    const userDetail = props.userDetails || getLocalData('user_detail')

    if (!userDetail) return (<OriginalComponent {...props} />)

    const { name, email, phone } = userDetail

    useEffect(() => {
      const canSetFromData = props.stripeUserForm && props.stripeUserForm.userInfoDefault && !(props.stripeUserForm.userInfoDefault.name)

      if (canSetFromData) {
        props.setStripeUserForm({
          ...props.stripeUserForm.userInfoDefault,
          name: userDetail.name || name,
          email: userDetail.email || email,
          phone: userDetail.phoneNumber || phone
        })
      }
    }, [])
    return (<OriginalComponent {...props} />)
  }

  const mapStateToProps = (state) => {
    const { firstName, lastName, email, phoneNumber } = getUserDetails(state)

    const dataToBeStore = { name: (firstName || '') + ' ' + (lastName || '') , email: email || '', phone: phoneNumber | '' }

    if (firstName && email) setLocalData('user_detail', dataToBeStore)

    return {
      userDetails: {
        name: (firstName && lastName) && `${firstName} ${lastName}`,
        email: email,
        phoneNumber: phoneNumber
      }
    }
  }

  const mapDispatchToProps = (dispatch) => {
    return {
      setAuth: (status) => dispatch(setAuth(status)) // WIP
    }
  }

  return connect(mapStateToProps, mapDispatchToProps)(NewComponent)
}



