import React, { useState } from 'react'
import {useStripe, useElements, PaymentElement} from '@stripe/react-stripe-js'
import { getLocalData, removeLocalData } from 'Shared/helpers/session'
import useApiCareerProCheckoutSubmitted from '../services/api/useApiCareerProCheckoutSubmitted'
import mixpanel from 'mixpanel-browser'
import { endPoints } from '../services/api/endPoints'

const StripeCheckoutForm = (props) => {
  const { stripeUserForm, returnUrl } = props
  const hasUserDetail = getLocalData('user_detail')
  const { apiCareerProCheckoutSubmitted } = useApiCareerProCheckoutSubmitted()
  const stripe = useStripe()
  const elements = useElements()

  const [showMsg, setShowMsg] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [allowSelect, setAllowSelect] = useState(true)
  const [error, setError] = useState(null)

  const handleSubmit = async (event) => {
    event.preventDefault()
    setAllowSelect(false)

    if (!stripe || !elements) {
      console.log('Stripe not loaded')
      setAllowSelect(true)
      return
    }
    removeLocalData('promocode_result_defaultCard')
    apiCareerProCheckoutSubmitted()
    let result
    setIsLoading(true)
    result = await stripe.confirmSetup({
      elements,
      confirmParams: {
        return_url: returnUrl,
        payment_method_data: {
          billing_details: {
            email: stripeUserForm.email,
            name: stripeUserForm.name,
            phone: stripeUserForm.mobile
          }
        }
      }
    })

    if (result) {
      setIsLoading(false)
      // props.history.push('/pricing?redirect_status=succeeded')
      // window.location.href = `${process.env.REACT_APP_ROOT_URL}/pricing?redirect_status=failed`
      // window.location.href = `http://linkst.local:7001/pricing?redirect_status=failed`
      console.log('redirect........................')
    }

    if (result.error) {
      window.location.href = `${returnUrl}?redirect_status=failed`
      console.log(result.error.message)
      setError(result.error.message)
      setShowMsg(true)
      setTimeout(() => {
        setShowMsg(false)
      }, 5000)
    }

    setAllowSelect(true)
  }

  return (
    <form>
      <PaymentElement />
      <div className='checkout_btnDiv'>
        <button className={`btnTheme btn_stripe ${isLoading && 'btn__disabled'}`} type='button' onClick={(e) => {
          handleSubmit(e)
        // mixpanel.track("Career Pro - Package Payment - Button Click - Submit")
        }}>{!isLoading && 'Submit'}</button>
        {isLoading && <div class={`PromoCodeLoader`} />}
      </div>
      {showMsg ? <small className='small error_msg_card'>{ error && error}</small> : ''}
    </form>
  )
}

export default StripeCheckoutForm



