import React, { Component } from 'react'
import { Helmet } from 'react-helmet'
import { compose } from 'redux'
import Container from 'Shared/components/Container'
import withTranslatios from 'Shared/hoc/withTranslations'
import JobsProductsConnector from 'Shared/connectors/Layout/JobProductsConnector'
import Testimonials from './components/Testimonials'
import Companies from './components/Companies'
import withScripts from 'TrackingScripts'
import { pricingPage } from 'Shared/constants/MetaTags'
import './Plans.scss'
import { ExternalRoutes } from 'Shared/constants/Routes'
import { withRouter } from 'react-router-dom'
import FAQConnectorEmployer from 'Employer/connectors/FAQConnectorEmployer'
import LocationCustomConnector from '../../connectors/LocationCustomConnector'
import CustomPlans from './components/CustomPlans'
import EmployerLPMainCTAButtonsHire from '../../../Employer/components/EmployerLPMainCTAButtons/EmployerLPMainCTAButtonsHire'
import mixpanel from 'mixpanel-browser'
import PlansModal from './PlansModal'

class Plans extends Component {
  componentDidMount () {
    // Track the page view with Mixpanel
    mixpanel.track('Page View - Pricing')
  }
  redirectToHelp = () => window.open(ExternalRoutes.help, '_blank')
  render (props) {
    const { t, isFreePlansPage, phoneNumber} = this.props
    const supportPhoneNumber = phoneNumber
    return (
      <div>

        <Helmet>
          <title>{pricingPage.title}</title>
          <meta name='description' content={pricingPage.description} />
          <meta property='og:title' content={pricingPage.title} />
          <meta property='og:type' content='website' />
          <meta property='og:image' content={pricingPage.image} />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:url' content={pricingPage.url} />
          <meta property='og:description' content={pricingPage.description} />
        </Helmet>
        <PlansModal />
        <Container>
          <div className='plans__heading-container'>
            <div className='columns'>
              <div className='column col-9 col-xs-12'>
                <div className='plans__heading'>{t('plansPage.heading')}</div>
                <div className='plans__heading'>{t('plansPage.description')}</div>
              </div>
            </div>
          </div>

          <div className='plans__cards-container'>
            <Companies />
            <JobsProductsConnector />
          </div>

        </Container>

        <Container>
          <Testimonials />
          <CustomPlans />
          {/* <FAQConnectorEmployer /> */}
        </Container>
        <section className='employers-page__create-account-section'>
          <div className='container'>
            <div className='employers-page__create-account'>
              <div className='employers-page__create-account-heading'>
                {t('employersPage.ctaSection.heading')}
              </div>
              <div className='employers-page__create-account-description'>
                {t('employersPage.ctaSection.description')}
              </div>
              <EmployerLPMainCTAButtonsHire isCentered />
            </div>
          </div>
        </section>
      </div>
    )
  }
}

export default compose(withScripts, withTranslatios, withRouter, LocationCustomConnector)(Plans)



