import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import greenTick from 'Shared/assets/images/other/green_tick.svg'

const data = [
  {
    heading: 'Big Data Insight Mailshots',
    description: 'Our team can set up targeted emails to your selected top-rated candidates, guaranteed to make your hiring process 3x faster.'
  },

  {
    heading: 'Talent Matching',
    description: 'Hand over the tedious task of sifting through resumes to our dedicated recruitment experts onboard and watch the magic happen!'
  },
  {
    heading: 'Emiratisation',
    description: 'Our team is trained to find the finest UAE talent from across the region, bolstering Emirati representation within your company.'
  }

]

const MediaPlan = ({t}) => {
  return (
    <div>
      <div className='media-plan-container'>
        <div className='media-plan-sub-container'>
          <div>
            <img src={greenTick} />
          </div>
          <div>
            <div className='media-plan-heading'>
              {t('customPlans.mediaPlans.point1.heading')}
            </div>
            <div className='media-plan-description'>
              {t('customPlans.mediaPlans.point1.dec')}
            </div>
          </div>
        </div>
      </div>

      <div className='media-plan-container'>
        <div className='media-plan-sub-container'>
          <div>
            <img src={greenTick} />
          </div>
          <div>
            <div className='media-plan-heading'>
              {t('customPlans.mediaPlans.point3.heading')}
            </div>
            <div className='media-plan-description'>
              {t('customPlans.mediaPlans.point3.dec')}
            </div>
          </div>
        </div>
      </div>

      <div className='media-plan-container'>
        <div className='media-plan-sub-container'>
          <div>
            <img src={greenTick} />
          </div>
          <div>
            <div className='media-plan-heading'>
              {t('customPlans.mediaPlans.point3.heading')}
            </div>
            <div className='media-plan-description'>
              {t('customPlans.mediaPlans.point3.dec')}
            </div>
          </div>
        </div>
      </div>

    </div>
  )
}

