import { useState } from 'react'
import { getRequest } from './commonApi'
import { endPoints } from './endPoints'
import { setLocalData } from 'Shared/helpers/session'

const useApiAddPromoCode = () => {
  const [loading, setLoading] = useState(true)
  const [data, setData] = useState(null)

  const pageUrl = window.location.pathname

  const apiAddPromoCode = async (promoCodeInput, url = pageUrl === '/pricing/checkout' ? endPoints.addPricingPromoCode : endPoints.addPromoCode) => {
    try {
      let res = await getRequest(url, { promo_code: promoCodeInput })

      if (res.success) {
        setData(res.res)
        setLocalData('promocode_result', res.data)
        setLocalData('promocode_result_defaultCard', res.data)
        return res.data
      }

      if (res.success == false) {
        setData(res.res)
        setLocalData('promocode_result', res)
        setLocalData('promocode_result_defaultCard', res.data)
        return res
      }

      setLocalData('promocode_result', '0')
    } catch (err) {
      console.log('Err', err)
    }
  }

  return { loading, data, apiAddPromoCode }
}

export default useApiAddPromoCode



