import React from 'react'
import prathnaPhoto from 'Shared/assets/images/other/prathna-jham.jpeg'
import withTranslations from 'Shared/hoc/withTranslations'
import samsungLogo from 'Shared/assets/images/logos/samsung-logo-about-us.svg'
import clientLogo from 'Shared/assets/images/icons/MP.jpg'

const Testimonial = ({ t }) => (
  <section className='plans__testimonial-container'>
    <div className='plans__testimonial-mark' />

    <img className='plans__testimonial-image' src={clientLogo} />

    <div className='plans__testimonial-description'>
      {t('plansPage.testimonial.description')}
    </div>
    <div>
      {/* <img src={prathnaPhoto} className='plans__testimonial-image quote_img' /> */}
      <div className='plans__testimonial-name'>{t('plansPage.testimonial.author')}</div>
      <div className='plans__testimonial-job-details'>{t('plansPage.testimonial.jobDetails')}</div>
    </div>
  </section>
)

export default withTranslations(Testimonial)



