import React, { Component } from 'react'
import { openIntercomWithNewMessage } from 'Intercom/components/IntercomComponent'
import { PrimaryButton } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import {
  getPriceWithCurrency,
  saveSelectedJobPosts,
  calculateJobsTotalSavings
} from 'Shared/helpers/payment'
import { NumericInput } from 'Shared/components/FormElements'
import { selectJobPostLimits, selectJobPostCandidateSearchLimits } from 'Shared/constants/Job'
import { isMobile } from 'Shared/helpers/mobile'
// import { formatAfterFive, parseNumericInput, formatNumericInputWithBoundary, formatMaxValue } from 'Shared/helpers/formats'
import { segmentSpec } from 'TrackingScripts/scripts/employer/segment'
import { setLocalData } from 'Shared/helpers/session'
class JobsProducts extends Component {
  componentDidMount () {
    this.props.fetchCurrentLocationIfNeeded()
    this.props.productDetails ? this.setState({data: this.props.productDetails}) : this.props.api().then(res => this.setState({data: res}))
  }

  state = {
    jobPostsAmount: 1,
    jobPostsCandidateSearchAmount: 1,
    helloWorld: 0,
    tooltipEventType: isMobile() ? 'click' : 'mouseenter',
    creatingNewJobOffer: false,
    data: []
  }

  inputProps = (e) => ({
    input: {
      onChange: value => this.setState({ jobPostsAmount: value }),
      value: this.state.jobPostsAmount
    },
    // format: formatMaxValue(this.state.jobPostsAmount),
    // format: formatAfterFive(this.state.jobPostsAmount),
    // parse: parseNumericInput(4),
    precision: 0,
    step: 1,
    meta: {},
    length: this.state.helloWorld
  })

  inputCandidateSearchProps = () => ({
    input: {
      onChange: value => this.setState({ jobPostsCandidateSearchAmount: value }),
      value: this.state.jobPostsCandidateSearchAmount
    },
    // format: formatAfterFive(this.state.jobPostsCandidateSearchAmount),
    // parse: parseNumericInput(6),
    precision: 0,
    step: 1,
    meta: {}
  })

  onSelectJobs = (pkgID, event) => {
    setLocalData('selected_package', pkgID)
    saveSelectedJobPosts(this.state.jobPostsAmount)
    segmentSpec.selectedJobPosts(this.state.jobPostsAmount, this.props.userEmail, this.props.userId)
    return this.props.history.push('/pricing/checkout')
  }

  onHandleContact = () => {
    return this.props.history.push('/contact')
  }

  onSelectCandidateSearchJobs = (pkgID, event) => {
    setLocalData('selected_package', pkgID)
    saveSelectedJobPosts(this.state.jobPostsCandidateSearchAmount)
    segmentSpec.selectedJobPosts(this.state.jobPostsCandidateSearchAmount, this.props.userEmail, this.props.userId)
    return this.props.history.push('/pricing/checkout')
  }

  handlePostJob = async () => {
    this.setState({ creatingNewJobOffer: true })
    await this.props.createNewEmptyJob()
    return this.setState({ creatingNewJobOffer: false })
  }

  handleFreeCta = () => {
    return this.props.isEmployer ? this.handlePostJob() : this.props.history.push('/employer/sign-up')
  }

  handleWithOutLogin = () => {
    return this.props.history.push('/employer/login')
  }

  getJobProductsCard = () => {
    const { t, price, currency, isEmployer, api } = this.props
    const jobPost = this.state.data.job_post
    const jobPostLength = typeof jobPost !== 'undefined' && jobPost.length

    const hasUserLogin = JSON.parse(localStorage.getItem('olive-app-token'))

    const formatNumericInputWithBoundary = boundary => value => value > boundary ? num => num - 1 + '+' : num => num
    const formatAfterFive = formatNumericInputWithBoundary(jobPostLength)
    const parseNumericInput = parseLimit => num => num === parseLimit + '+' ? parseLimit + 1 : num

    console.log('Product Details: ', this.state.data)
    const { jobPostsAmount } = this.state

    // const jobPostFormatData = formatAfterFive(jobPostsAmount)

    console.log('Product Details jobPostsAmount: ', jobPostsAmount)
    const isBetweenOneAndFive = jobPostsAmount >= 1 && jobPostsAmount <= jobPostLength
    let postFind = isBetweenOneAndFive && jobPost && jobPost.find(elem => elem.quantity === jobPostsAmount)
    let postCurrency = postFind ? postFind.currency.toUpperCase() : '$'
    let postPrice = postFind ? postFind.price : '0'
    // const credits = postFind.features
    // setLocalData('selected_package', postFind)
    console.log('postFind::', postFind)
    const savings = calculateJobsTotalSavings(jobPostsAmount, postPrice)
    switch (true) {
      case this.props.isFreePlansPage:
        return (
          <div className='job-products__card job-products__card--free'>
            <div className='job-products__card-heading'>
              <div>
                <div className='job-products__card-heading-title'>
                  {t('plansPage.products.jobPost.title')}
                </div>
                <div className='job-products__card-heading-description'>
                  {t('plansPage.products.jobPost.description')}
                </div>
              </div>
            </div>
            <div className='job-products__card-content'>
              <div className='job-products__card-price'>
                Free
              </div>
            </div>
            <div className='job-products__card-btn-container'>
              <PrimaryButton disabled={this.state.creatingNewJobOffer} onClick={this.handleFreeCta}>
                {isEmployer ? <span><i className='ion-md-add' />&nbsp;{t('common.postJob')}</span> : t('plansPage.products.jobPost.freeBtn')}
              </PrimaryButton>
            </div>
          </div>
        )
      default:
        return (<React.Fragment>
          <div className='job-products__card'>
            <div className='job-products__card-heading'>
              <div>
                <div className='job-products__card-heading-title'>
                  {t('plansPage.products.jobPost.title')}
                </div>
                <div className='job-products__card-heading-description'>
                  {t('plansPage.products.jobPost.description')}
                </div>
              </div>
            </div>
            <div className='job-products__card-content'>
              {/* <div className='job-products__card-price-label'>{t('plansPage.products.jobPost.total')}</div> */}
              <div className='job-products__card-price'>
                {
                  isBetweenOneAndFive
                    ? getPriceWithCurrency(postPrice, postCurrency)
                    : t('plansPage.products.jobPost.customPricing')
                }
              </div>
              {
                <span className='job-products__card-price-vat'>{ isBetweenOneAndFive && t('plansPage.products.jobPost.vatInclusive') }</span>
              }
              <div className='job-products__card-select-jobs'>
                <div className='job-products__card-select-jobs-label'>
                  <div className='job-products__card-select-jobs-label--green'>
                    <span>{postFind.discount == null ? <span className='blackClr'>Save with multiple job posts</span> : postFind.discount}</span>
                  </div>
                </div>
                <NumericInput {...this.inputProps()}
                  format={formatAfterFive(jobPostsAmount)}
                  parse={parseNumericInput(5)}
                  min={1} max={jobPostLength + 1} />
              </div>
            </div>
            <div className='job-products__card-btn-container'>
              <PrimaryButton onClick={jobPostsAmount > jobPostLength ? this.onHandleContact : !hasUserLogin ? this.handleWithOutLogin : this.onSelectJobs.bind(this, postFind)}>
                {jobPostsAmount > jobPostLength ? t('common.talkToSales') : t('common.select')}
              </PrimaryButton>
            </div>
          </div>
        </React.Fragment>)
    }
  }

  getCandidateSearch = () => {
    const { t, price, currency, isEmployer, api } = this.props
    const jobPostCandidateSearch = this.state.data.candidate_search
    const lastElement = jobPostCandidateSearch && jobPostCandidateSearch[jobPostCandidateSearch.length - 1]
    const jobPostCandidateSearchLength = typeof jobPostCandidateSearch !== 'undefined' && jobPostCandidateSearch.length

    const hasUserLogin = JSON.parse(localStorage.getItem('olive-app-token'))

    const formatNumericInputWithBoundarySearch = boundary => value => value > boundary ? num => num - 1 + '+' : num => num
    const formatAfterFiveSearch = formatNumericInputWithBoundarySearch(jobPostCandidateSearchLength)
    const parseNumericInputSearch = parseLimit => num => num === parseLimit + '+' ? parseLimit + 1 : num

    console.log('jobPostCandidateSearch::', jobPostCandidateSearch)
    const { jobPostsCandidateSearchAmount } = this.state
    const isBetweenOneAndFiveCandidateSearch = jobPostsCandidateSearchAmount >= 1 && jobPostsCandidateSearchAmount <= jobPostCandidateSearchLength

    // formatNumericInputWithBoundary(jobPostCandidateSearchLength)
    // const jobPostFormatDataSearch = formatAfterFive(jobPostsCandidateSearchAmount(jobPostCandidateSearchLength))
    // jobPostFormatDataSearch(jobPostsCandidateSearchAmount)
    let postFindCandidateSearch = isBetweenOneAndFiveCandidateSearch && jobPostCandidateSearch && jobPostCandidateSearch.find(elem => elem.quantity === jobPostsCandidateSearchAmount)
    let postCurrencyCandidateSearch = postFindCandidateSearch ? postFindCandidateSearch.currency.toUpperCase() : '$'
    let postPriceCandidateSearch = postFindCandidateSearch ? postFindCandidateSearch.price : '0'
    // setLocalData('selected_package', postFindCandidateSearch)
    console.log('postFindCandidateSearch::', postFindCandidateSearch)
    const credits = postFindCandidateSearch ? postFindCandidateSearch.features : lastElement && lastElement.features + '+'
    const savings = calculateJobsTotalSavings(jobPostsCandidateSearchAmount, postPriceCandidateSearch)
    switch (true) {
      case this.props.isFreePlansPage:
        return (
          <div className='job-products__card job-products__card--free'>
            <div className='job-products__card-heading'>
              <div>
                <div className='job-products__card-heading-title'>
                  {t('plansPage.products.candidate_search.title')}
                </div>
                <div className='job-products__card-heading-description'>
                  {t('plansPage.products.candidate_search.description')}
                </div>
              </div>
            </div>
            <div className='job-products__card-content'>
              <div className='job-products__card-price'>
                Free
              </div>
            </div>
            <div className='job-products__card-btn-container'>
              <PrimaryButton disabled={this.state.creatingNewJobOffer} onClick={this.handleFreeCta}>
                {isEmployer ? <span><i className='ion-md-add' />&nbsp;{t('common.postJob')}</span> : t('plansPage.products.jobPost.freeBtn')}
              </PrimaryButton>
            </div>
          </div>
        )
      default:
        return (<React.Fragment>
          <div className='job-products__card'>
            <div className='job-products__card-heading'>
              <div>
                <div className='job-products__card-heading-title'>
                  {t('plansPage.products.candidate_search.title')}
                </div>
                <div className='job-products__card-heading-description'>
                  {t('plansPage.products.candidate_search.description')}
                </div>
              </div>
            </div>
            <div className='job-products__card-content'>
              {/* <div className='job-products__card-price-label'>{t('plansPage.products.candidate_search.total')}</div> */}
              <div className='job-products__card-price'>
                {postFindCandidateSearch
                  ? isBetweenOneAndFiveCandidateSearch
                    ? getPriceWithCurrency(postPriceCandidateSearch, postCurrencyCandidateSearch)
                    : t('plansPage.products.candidate_search.customPricing') : t('plansPage.products.candidate_search.title')
                }

              </div>
              {
                <span className='job-products__card-price-vat'>{ isBetweenOneAndFiveCandidateSearch && t('plansPage.products.candidate_search.vatInclusive') }</span>
              }
              <div className='job-products__card-select-jobs'>
                <div className='job-products__card-select-jobs-label'>
                  <div className='job-products__card-select-jobs-label--green'>
                    <span>{postFindCandidateSearch ? postFindCandidateSearch.discount == null ? <span className='blackClr'>Save with multiple cerdits</span> : postFindCandidateSearch.discount : <span className='blackClr'>Save with multiple credits</span>}</span>
                  </div>
                </div>
                <NumericInput {...this.inputCandidateSearchProps()}
                  credits={credits}
                  format={formatAfterFiveSearch(jobPostsCandidateSearchAmount)}
                  parse={parseNumericInputSearch(jobPostCandidateSearchLength)}
                  min={1} max={jobPostCandidateSearchLength + 1} />
              </div>
            </div>
            <div className='job-products__card-btn-container'>
              <PrimaryButton onClick={jobPostsCandidateSearchAmount > jobPostCandidateSearchLength ? this.onHandleContact : !hasUserLogin ? this.handleWithOutLogin : this.onSelectCandidateSearchJobs.bind(this, postFindCandidateSearch)}>
                {jobPostsCandidateSearchAmount > jobPostCandidateSearchLength ? t('common.talkToSales') : t('common.select')}
              </PrimaryButton>
            </div>
          </div>
        </React.Fragment>)
    }
  }
  render () {
    const { t } = this.props
    return (
      <section>
        <div className='columns'>
          <div className='column col-6 col-xs-12'>
            {this.getJobProductsCard()}

          </div>
          <div className='column col-6 col-xs-12'>
            {this.getCandidateSearch()}

          </div>
          <div className='column col-3 col-xs-12' />

        </div>
      </section>
    )
  }
}

export default withTranslations(JobsProducts)



